@charset "utf-8";

/* CSS Document */



*,
*:before,
*:after {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
}
html{font-size: 20px;}

body {
	font-family: "\5FAE\8F6F\96C5\9ED1";
	margin: 0px auto;
}

ul,
li {
	list-style: none;
}

a {
	text-decoration: none;
}

img {
	border: none;
	display: block;
}

.clear {
	clear: both;
	display: block;
	font-size: 0;
	height: 0;
	line-height: 0;
	overflow: hidden;
}

input,
button,
select {
	border: none;
	outline: none;
}

input {
	background: transparent;
}
